@import '../fonts/fontello/css/fontello.css';

.container {
  max-width: 1600px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
}

html body,
html div,
html dl,
html dt,
html dd,
html ul,
html li,
html h1,
html h2,
html h3,
html h4,
html h5,
html h6,
html pre,
html code,
html form,
html fieldset,
html input,
html textarea,
html p,
html blockquote,
html th,
html td {
  margin: 0;
  padding: 0;
}

html table {
  border-collapse: collapse;
  border-spacing: 0;
}

html fieldset,
html img,
html abbr {
  border: 0;
}

html address,
html caption,
html cite,
html code,
html dfn,
html em,
html strong,
html th,
html var {
  font-style: normal;
  font-weight: normal;
}

html ul li {
  list-style: none;
}

html caption,
html th {
  text-align: left;
}

html h1,
html h2,
html h3,
html h4,
html h5,
html h6 {
  font-size: 100%;
  font-weight: normal;
}

html sup {
  vertical-align: text-top;
}

html sub {
  vertical-align: text-bottom;
}

html input,
html textarea,
html select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

html legend {
  color: #000;
}

html article,
html aside,
html details,
html figcaption,
html figure,
html footer,
html header,
html hgroup,
html menu,
html nav,
html section,
html main {
  display: block;
}

html img {
  max-width: 100%;
  height: auto;
}

body {
  background-image: url(../img/back.jpg);
  font-family: 'Oswald';
}

.header {
  background-image: url(../img/header-bg.png);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  min-height: 150px;
  position: relative;
}

.navbar {
  position: absolute;
  width: 100%;
  top: 91px;
  font-family: 'Oswald', sans-serif;
  background-color: #000000;
  opacity: .8;
  z-index: 10;
}

#menu {
  z-index: 1000;
}

.menu {
  display: flex;
  justify-content: space-around;
  text-transform: capitalize;
}

.menu li {
  padding: 10px;
}

.menu li a {
  text-decoration: none;
  color: #ffffff;
  font-size: 26px;
  transition: 0.25s ease-in-out;
}

.menu li a:hover {
  color: #CB0001;
}

.mobile-h1 {
  display: none;
}

.mobi-menu {
  display: none;
}

.mobile-menu-opened {
  position: relative;
  top: 20px;
  left: 0px;
  width: 50px;
  height: 50px;
  z-index: 2;
  display: block;
  cursor: pointer;
  border: none;
  background: inherit;
}

.mobile-menu-opened:before {
  content: "";
  position: absolute;
  top: 0;
  right: 10px;
  width: 28px;
  height: 4px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}

body {
  overflow-x: hidden;
}

.main {
  background-color: #e9e9e9;
  max-width: 1170px;
  margin: 0 auto;
}

.grid-main {
  padding-top: 50px;
  display: flex;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;
}

.content {
  padding-left: 20px;
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
  width: calc(66.66666666666667% - 10px);
}

.content h1 {
  font-size: 34px;
}

.sidebar {
  box-sizing: border-box;
  margin-left: 5px;
  margin-right: 5px;
  word-wrap: break-word;
  width: calc(33.333333333333336% - 10px);
}

.products-items {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.products-item {
  position: relative;
  text-align: center;
  margin-right: 20px;
  margin-top: 30px;
}

.products-item span {
  display: block;
  font-size: 11px;
}

.products-item h2 {
  font-size: 18px;
  margin-bottom: 5px;
}

.products-item img {
  border-radius: 3px;
  width: 111px;
  min-height: 156px;
  z-index: 109;
}

.search-content {
  width: 250px;
  margin-right: 53px;
  border: 1px solid #c0c0c0;
  background-color: #eeeeee;
  padding: 15px;
  font-family: 'Arial', sans-serif;
  font-size: 14px;
  background-image: url(../img/search.png);
  background-repeat: no-repeat;
  background-position: 220px center;
}

.privacy-policy {
  font-size: 11px;
  font-family: 'Arial', sans-serif;
  line-height: 1.4;
  text-align: center;
  padding-left: 5px;
}

.btn-product {
  box-sizing: border-box;
  margin: 0;
  outline: none;
  cursor: pointer;
  text-transform: capitalize;
  border: 0;
  border-radius: 4px;
  padding: 5px 20px 5px 20px;
  font-size: 12px;
  color: #ffffff;
  background-color: #555555;
  transition: all 500ms ease;
}

.btn-product:hover {
  background: rgba(0, 0, 0, 0);
  color: #555555;
  box-shadow: inset 0 0 0 3px #555555;
}

.btn-form {
  box-sizing: border-box;
  margin: 0;
  outline: none;
  cursor: pointer;
  text-transform: capitalize;
  border: 0;
  border-radius: 9px;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 35px;
  padding-right: 35px;
  font-size: 14px;
  color: #818181;
  background: linear-gradient(90deg, #f2f2f2, #e6e6e6, 100%);
  font-weight: 600;
  transition: .2s;
}

.btn-form:not(:active) {
  box-shadow: 0 3px 0 1px #bdbdbd;
}

.search-h1 {
  display: flex;
  justify-content: space-between;
}

.search-h1 h1 {
  text-transform: capitalize;
}

form.form-reg {
  display: flex;
  flex-direction: column;
}

.btn-form {
  align-self: flex-end;
  margin-top: 15px;
}

.products-h2 {
  font-size: 26px;
  position: relative;
}

.products-h2:before {
  content: url(../img/minus.png);
  position: absolute;
  top: -5px;
  left: -25px;
  cursor: pointer;
}

.popup-wrap {
  position: absolute;
  top: 5px;
  left: -17px;
}

.popup {
  display: none;
  box-sizing: border-box;
  padding-top: 20px;
  text-align: center;
  color: #d3d3d3;
  font-family: 'Arial', sans-serif;
  background-color: #000000;
  opacity: 0.788;
  position: relative;
  left: 645px;
  top: 400px;
  width: 248px;
  height: 109px;
  z-index: 1000;
  border-radius: 8px;
}

.popup:after,
.popup:before {
  top: 100%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}

.popup:after {
  border-color: rgba(0, 0, 0, 0);
  border-top-color: #000;
  border-width: 15px;
  left: 50%;
  transform: translateX(-50%);
}

.popup:before {
  border-color: rgba(0, 0, 0, 0);
  border-top-color: ;
  border-width: 15px;
  left: 50%;
  transform: translateX(-50%);
}

.popup h2 {
  font-size: 14px;
  font-weight: bold;
}

.popup p {
  padding-top: 8px;
  font-size: 12px;
  font-weight: normal;
}

.sidebar .sidebar-h1 {
  padding-left: 30px;
  font-size: 28px;
  color: #000000;
}

.sidebar .item-sidebar {
  border: 1px solid #cccccc;
  background-color: #DBDBDB;
  margin-bottom: 30px;
  box-sizing: border-box;
  padding: 10px;
  text-align: left;
  border-radius: 10px;
  background-color: #e3e3e3;
  opacity: 0.851;
  width: 100%;
  min-height: 328px;
  z-index: 55;
  transition: box-shadow 0.9s;
}

.sidebar .item-sidebar:hover {
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

.sidebar input {
  display: block;
  margin: 10px;
  box-sizing: border-box;
  background-color: #eeeeee;
  border: 1px solid #c0c0c0;
  font-family: 'Arial', sans-serif;
  font-size: 14px;
  width: 300px;
  padding: 15px;
}

.o-monitor {
  position: relative;
  font-family: 'Oswald', sans-serif;
  color: #c6c6c6;
}

.o-monitor .sidebar-h1 {
  font-size: 28px;
  padding-left: 30px;
  color: #c6c6c6;
}

.link-products {
  color: #444444;
  font-family: 'Arial', sans-serif;
}

.content-prod {
  margin-top: 50px;
  text-align: center;
}

.load {
  font-size: 16px;
  font-family: 'Arial', sans-serif;
  color: #4c4c4d;
  position: absolute;
  top: 75%;
  left: 50%;
  transform: translateX(-50%);
  font-weight: 500;
}

.privacy-policy a {
  color: #444444;
}

.holder {
  width: 100%;
}

.footer-socials {
  background-color: #000000;
}

.footer-socials-menu {
  margin-left: 50px;
  display: flex;
  justify-content: space-between;
}

.footer-socials-menu a {
  display: block;
  padding-right: 50px;
}

.footer-socials-menu a:last-child {
  padding-right: none;
}

.footer-socials-menu a:hover {
  color: #ffffff;
}

.footer-socials-menu i {
  color: #444444;
  font-size: 40px;
}

.footer-socials-menu li {
  list-style: none;
}

.nav-left {
  margin-left: 90px;
  width: auto;
  padding-top: 35px;
  padding-bottom: 35px;
}

.nav-right {
  margin-right: 100px;
  width: auto;
}

.nav-right p {
  color: #444444;
  font-size: 30px;
}

.menu-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.footer {
  background-color: #181818;
}

.footer-user-menu {
  padding-top: 25px;
  padding-bottom: 25px;
  display: flex;
  justify-content: space-around;
}

.footer-user-menu ul {
  color: #606060;
}

.footer-user-menu ul li {
  font-size: 16px;
}

.footer-user-menu p {
  font-size: 26px;
  color: #444444;
}

.footer-user-menu .support {
  font-size: 14px;
  text-align: right;
}

.support-browsers {
  align-self: flex-end;
  z-index: 200;
}

.container-f {
  width: auto;
}

.copy {
  display: none;
}

.holder {
  position: absolute;
  left: 0px;
  top: 0px;
  bottom: 0px;
  right: 0px;
  width: 100%;
  height: 100%;
}

.preloader {
  /* size */
  width: 100px;
  height: 100px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  animation: rotatePreloader 2s infinite ease-in;
}

@keyframes rotatePreloader {
  0% {
    transform: translateX(-50%) translateY(-50%) rotateZ(0deg);
  }

  100% {
    transform: translateX(-50%) translateY(-50%) rotateZ(-360deg);
  }
}

.preloader div {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0;
}

.preloader div:before {
  content: "";
  position: absolute;
  left: 50%;
  top: 0%;
  width: 10%;
  height: 10%;
  background-color: #4c4c4d;
  transform: translateX(-50%);
  border-radius: 50%;
}

.preloader div:nth-child(1) {
  transform: rotateZ(0deg);
  animation: rotateCircle1 2s infinite linear;
  z-index: 9;
}

@keyframes rotateCircle1 {
  0% {
    opacity: 0;
  }

  0% {
    opacity: 1;
    transform: rotateZ(36deg);
  }

  7% {
    transform: rotateZ(0deg);
  }

  57% {
    transform: rotateZ(0deg);
  }

  100% {
    transform: rotateZ(-324deg);
    opacity: 1;
  }
}

.preloader div:nth-child(2) {
  transform: rotateZ(36deg);
  animation: rotateCircle2 2s infinite linear;
  z-index: 8;
}

@keyframes rotateCircle2 {
  5% {
    opacity: 0;
  }

  5.0001% {
    opacity: 1;
    transform: rotateZ(0deg);
  }

  12% {
    transform: rotateZ(-36deg);
  }

  62% {
    transform: rotateZ(-36deg);
  }

  100% {
    transform: rotateZ(-324deg);
    opacity: 1;
  }
}

.preloader div:nth-child(3) {
  transform: rotateZ(72deg);
  animation: rotateCircle3 2s infinite linear;
  z-index: 7;
}

@keyframes rotateCircle3 {
  10% {
    opacity: 0;
  }

  10.0002% {
    opacity: 1;
    transform: rotateZ(-36deg);
  }

  17% {
    transform: rotateZ(-72deg);
  }

  67% {
    transform: rotateZ(-72deg);
  }

  100% {
    transform: rotateZ(-324deg);
    opacity: 1;
  }
}

.preloader div:nth-child(4) {
  transform: rotateZ(108deg);
  animation: rotateCircle4 2s infinite linear;
  z-index: 6;
}

@keyframes rotateCircle4 {
  15% {
    opacity: 0;
  }

  15.0003% {
    opacity: 1;
    transform: rotateZ(-72deg);
  }

  22% {
    transform: rotateZ(-108deg);
  }

  72% {
    transform: rotateZ(-108deg);
  }

  100% {
    transform: rotateZ(-324deg);
    opacity: 1;
  }
}

.preloader div:nth-child(5) {
  transform: rotateZ(144deg);
  animation: rotateCircle5 2s infinite linear;
  z-index: 5;
}

@keyframes rotateCircle5 {
  20% {
    opacity: 0;
  }

  20.0004% {
    opacity: 1;
    transform: rotateZ(-108deg);
  }

  27% {
    transform: rotateZ(-144deg);
  }

  77% {
    transform: rotateZ(-144deg);
  }

  100% {
    transform: rotateZ(-324deg);
    opacity: 1;
  }
}

.preloader div:nth-child(6) {
  transform: rotateZ(180deg);
  animation: rotateCircle6 2s infinite linear;
  z-index: 4;
}

@keyframes rotateCircle6 {
  25% {
    opacity: 0;
  }

  25.0005% {
    opacity: 1;
    transform: rotateZ(-144deg);
  }

  32% {
    transform: rotateZ(-180deg);
  }

  82% {
    transform: rotateZ(-180deg);
  }

  100% {
    transform: rotateZ(-324deg);
    opacity: 1;
  }
}

.preloader div:nth-child(7) {
  transform: rotateZ(216deg);
  animation: rotateCircle7 2s infinite linear;
  z-index: 3;
}

@keyframes rotateCircle7 {
  30% {
    opacity: 0;
  }

  30.0006% {
    opacity: 1;
    transform: rotateZ(-180deg);
  }

  37% {
    transform: rotateZ(-216deg);
  }

  87% {
    transform: rotateZ(-216deg);
  }

  100% {
    transform: rotateZ(-324deg);
    opacity: 1;
  }
}

.preloader div:nth-child(8) {
