body {
  width: 100%;
  height: 100%;
}

.slideout-menu {
  position: fixed;
  top: 0;
  bottom: 0;
  width: 256px;
  min-height: 100vh;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  z-index: 0;
  display: none;
}

.slideout-menu-left {
  left: 0;
}

.slideout-menu-right {
  right: 0;
}

.slideout-panel {
  position: relative;
  z-index: 1;
  will-change: transform;
  background-color: #FFF;
  /* A background-color is required */
  min-height: 100vh;
}

.slideout-open,
.slideout-open body,
.slideout-open .slideout-panel {
  overflow: hidden;
}

.slideout-open .slideout-menu {
  display: block;
}

@media screen and (max-width: 1200px) {
  .container {
    padding-left: 30px;
    padding-right: 30px;
  }
}

@media screen and (max-width: 992px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }

  .main {
    width: auto;
  }

  .products-items {
    display: flex;
    flex-wrap: wrap;
    margin-left: -5px;
    margin-right: -5px;
  }

  .products-item {
    box-sizing: border-box;
    margin-left: 5px;
    margin-right: 5px;
    word-wrap: break-word;
    width: calc(50% - 10px);
  }

  .sidebar input {
    width: 210px;
  }

  .nav-right p {
    display: none;
  }
}

@media screen and (max-width: 720px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }

  .content {
    width: calc(100% - 10px);
  }

  .sidebar {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: calc(100% - 10px);
  }

  .search-content {
    display: none;
  }

  .search-h1 {
    display: none;
  }

  .sidebar .item-sidebar {
    border-radius: 0;
    width: auto;
    border: none;
    border-top: 1px solid #cccccc;
  }

  .sidebar .item-sidebar:hover {
    box-shadow: none;
  }

  .o-monitor {
    margin-top: 45px;
  }

  .item-sidebar--mob {
    display: none;
  }

  .content-prod {
    display: none;
  }

  .nav-left {
    margin-left: 0;
  }

  .nav-right p {
    display: none;
  }

  .menu-footer {
    justify-content: center;
  }

  .copy {
    display: block;
    text-align: center;
    color: #444444;
    font-size: 32px;
    padding-bottom: 50px;
  }
}

@media screen and (max-width: 576px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }

  .menu {
    display: none;
  }

  .mobile-h1 {
    display: block;
    font-size: 28px;
    color: #ffffff;
    text-align: center;
    line-height: 60px;
  }

  .mobi-menu {
    display: block;
  }

  .footer-socials-menu {
    margin-left: 0;
  }

  .footer-socials-menu a {
    padding-right: 10px;
  }

  .nav-right {
    margin-right: 0;
  }

  .footer-user-menu {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .footer-user-menu p {
    padding-bottom: 20px;
  }

  .footer-user-menu .support {
    text-align: center;
  }

  .support-browsers {
    align-self: center;
  }

  .user-data {
    margin-top: 40px;
    padding-bottom: 50px;
  }

  .navigation {
    margin-top: 50px;
    padding-bottom: 50px;
  }

  .footer {
    display: flex;
    flex-direction: column;
  }
}