.header{
	background-image: url(../img/header-bg.png);
	background-position: center center;
	background-repeat: no-repeat;
	background-size: cover;
	width: 100%;
	min-height: 150px;
	position: relative;
}
.navbar{
	position: absolute;
	width: 100%;
	top: 91px;
	font-family: 'Oswald', sans-serif;
	background-color: @black;
	opacity: .8;
	z-index: 10;
	
}
#menu{
	z-index: 1000;
}
.menu{
	.xs-block({
		display: none;
	});
	display: flex;
	justify-content: space-around;
	text-transform: capitalize;
	li{
		padding: 10px;
		a{
			text-decoration: none;
			color: @white;
			font-size: 26px;
			transition: .25s ease-in-out;
			&:hover{
				color: #CB0001;
			}
		}
	}
}

.mobile-h1{
	display: none;
	.xs-block({
	display: block;
	font-size: 28px;
	color: @white;
	text-align: center;
	line-height: 60px;
	});
}
.mobi-menu{
	display: none;
	.xs-block({
		display: block;
	});
}
.mobile-menu-opened{
	position: relative;
    top: 20px;
    left: 0px;
    width: 50px;
    height: 50px;
    z-index: 2;
    display: block;
    cursor: pointer;
		border: none;
		background: inherit;
		&:before{
			content: "";
			position: absolute;
			top: 0;
			right: 10px;
			width: 28px;
			height: 4px;
			background-color: @black;
			box-shadow: 0 8px 0 0 @black, 0 16px 0 0 @black;
		}
}
	
