.footer-socials{
	background-color: @black;
}
.footer-socials-menu{
	.xs-block({
		margin-left: 0;
	});
	margin-left: 50px;
	display: flex;
	justify-content: space-between;
	a {
		.xs-block({
			padding-right: 10px;
		});
		display: block;
		padding-right: 50px;
		&:last-child{
			padding-right: none;
		}
		&:hover{
			color: @white;
		}
	}
	i{
		color: @link;
		font-size: 40px;
		
	}
	li{
		list-style: none;
	}

}
.nav-left{
	margin-left: 90px;
	width: auto;
	padding-top: 35px;
	padding-bottom: 35px;
	.sm-block({
		margin-left:0;
		margin-left: 0;
	});
}
.nav-right{
	margin-right: 100px;
	width: auto;
	.xs-block({
		margin-right: 0;
	});
	p{
		color: @link;
		font-size: 30px;
		.sm-block({
			display: none;
		});
		.md-block({
			display: none;
		});
	}


}
.menu-footer{
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-wrap: wrap;
	.sm-block({
		justify-content: center;
	});
}
.footer{
	background-color: @footer-bg;
}
.footer-user-menu{
	.xs-block({
		display: flex;
		flex-direction: column;
		align-items: center;
		text-align: center;
	});
	padding-top: 25px;
	padding-bottom: 25px;
	display: flex;
	justify-content: space-around;
	ul{
		color: @footer-menu;
		li{
			font-size: 16px;
		}
	}
	p{
		font-size: 26px;
		color: @link;
		.xs-block({
			padding-bottom: 20px;
		});
	}
	.support{
		font-size: 14px;
		text-align: right;
		.xs-block({
			text-align: center;
		});
	}
}
.support-browsers{
	.xs-block({
		align-self: center;
	});
	align-self: flex-end;
	z-index: 200;
}
.container-f{
	width: auto;
}
.user-data{
	.xs-block({
		margin-top: 40px;
		padding-bottom: 50px;
	});
}
.navigation{
	.xs-block({
		margin-top: 50px;
		padding-bottom: 50px;
	});
}
.footer{
	.xs-block({
		display: flex;
		flex-direction: column;
	});
}
.copy{
	display: none;
	.sm-block({
		display: block;
		text-align: center;
		color: @link;
		font-size: 32px;
		padding-bottom: 50px;
	});
}