body{
	overflow-x: hidden 
}
.main {
	background-color: @main-bg;
	max-width: 1170px;
	margin: 0 auto;
	.md-block({
		width: auto;
	});
}
.grid-main{
	padding-top: 50px;
	.row-flex();
}
.content{
	padding-left: 20px;
	box-sizing: border-box;
	.col();
	.size(8);
	.size-sm(12);
	h1{
		font-size: 34px;
	}
}

.sidebar{
	.col();
	.size(4);
	.sm-block({
		display: flex;
		flex-direction: column;
		align-items: center;
		.size(12);
	});
}
.products-items{
	display: flex;
	flex-wrap: wrap;
	justify-content: flex-start;
	//320px
	.md-block({
		.row-flex();
	});
}
.products-item {
	.md-block({
		.col();
		.size(6);
	});
	position: relative;
	text-align: center;
	margin-right: 20px;
	margin-top: 30px;
	span{
		display: block;
		font-size: 11px;
	}
	h2{
		font-size: 18px;
		margin-bottom: 5px;
	}
	img {
	border-radius: 3px;
	width: 111px;
  min-height: 156px;
  z-index: 109;
	}
}
.search-content{
	width: 250px;
	margin-right: 53px;
	border: 1px solid  @border-input;
	background-color: @placehold;
	padding: 15px;
	font-family: 'Arial', sans-serif;
	font-size: 14px;
	background-image: url(../img/search.png);
	background-repeat: no-repeat;
	background-position: 220px center;
	.sm-block({
		display: none;
	});
}
.privacy-policy{
	font-size: 11px;
	font-family: 'Arial', sans-serif;
	line-height: 1.4;
	text-align: center;
	padding-left: 5px;
}
.btn-product{
	box-sizing: border-box;
	margin: 0;
	outline: none;
	cursor: pointer;
	text-transform: capitalize;
	border: 0;
	border-radius: 4px;
	padding: 5px 20px 5px 20px;
	font-size: 12px;
	color: @white;
	background-color: @btn;
	transition: all 500ms ease; 
	&:hover {
    background: rgba(0,0,0,0);
    color: @btn;
		box-shadow: inset 0 0 0 3px @btn;
}
}
.btn-form{
	box-sizing: border-box;
	margin: 0;
	outline: none;
	cursor: pointer;
	text-transform: capitalize;
	border: 0;
	border-radius: 9px;
	padding-top: 10px;
	padding-bottom: 10px;
	padding-left: 35px;
	padding-right: 35px;
	font-size: 14px;
	color: #818181;
	background: linear-gradient(90deg, #f2f2f2,#e6e6e6, 100%);
	font-weight: 600;
	transition: .2s;

		}
		.btn-form:not(:active) {
			box-shadow: 0 3px 0 1px #bdbdbd;}

.search-h1{
	.sm-block({
		display: none;
	});
	display: flex;
	justify-content: space-between;	
	h1{
		text-transform: capitalize;
	}
}
form.form-reg {
	display: flex;
	flex-direction: column;

}
.btn-form{
	align-self: flex-end;
	margin-top: 15px;
}
.products-h2{
	font-size: 26px;
	position: relative;
	&:before{
		content: url(../img/minus.png);
		position: absolute;
		top: -5px;
		left: -25px;
		cursor: pointer;
	}
}
//Popup
.popup-wrap{
	position: absolute;
	top: 5px;
	left: -17px;
}
.popup{
		display: none;
		box-sizing: border-box;
		padding-top: 20px;
		text-align: center;
		color: @txt;
		font-family: 'Arial',sans-serif;
		background-color: @black;
		opacity: 0.788;
		position: relative;
		left: 645px;
		top: 400px;
		width: 248px;
		height: 109px;
		z-index: 1000;
		border-radius: 8px;
			&:after, &:before {
			top: 100%;
			border: solid transparent;
			content: " ";
			height: 0;
			width: 0;
			position: absolute;
			pointer-events: none;
		}
		
		&:after {
			border-color: rgba(0, 0, 0, 0);
			border-top-color: #000;
			border-width: 15px;
			left: 50%;
			transform: translateX(-50%);
			
		}
		&:before {
			border-color: rgba(0, 0, 0, 0);
			border-top-color: ;
			border-width: 15px;
			left: 50%;
			transform: translateX(-50%);
		}

		h2{
			font-size: 14px;
			font-weight: bold;
		}
		p{
			padding-top: 8px;
			font-size: 12px;
			font-weight: normal;
		}
}

//  

.sidebar{
.xs-block(){
	background-color: red;
}

	.sidebar-h1{
		
		padding-left: 30px; 
		font-size: 28px;
		color: @black;
	}
.item-sidebar {
	.sm-block({
		border-radius: 0;
		width: auto;
		border: none;
		border-top: 1px solid @grey-border;
		&:hover{
			box-shadow: none;
			}
	});
	border: 1px solid @grey-border;
	background-color: @grey;
	margin-bottom: 30px;
	box-sizing: border-box;
	padding: 10px;
	text-align: left;
  border-radius: 10px;
  background-color: rgb(227, 227, 227);
  opacity: 0.851;
  width: 100%;
  min-height: 328px;
	z-index: 55;
	transition: box-shadow .9s;
	&:hover{
	box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
	}

}
	input{
		display: block;
		margin: 10px;
		box-sizing: border-box;
		background-color: @placehold;
		border: 1px solid @border-input;
		font-family: 'Arial', sans-serif;
		font-size: 14px;
		width: 300px;
		padding: 15px;
		.md-block({
			width: 210px;
		});
	}
}
.o-monitor{
	position: relative;
	font-family: 'Oswald', sans-serif;
	color: @txt-monitor;
	.sm-block({
		margin-top: 45px;
	});
	.sidebar-h1{
		font-size: 28px;;
		padding-left: 30px;
		color: @txt-monitor;
	}
}
.link-products{
	color: @link;
	font-family: 'Arial',sans-serif;
}
.content-prod{
	margin-top: 50px;
	text-align: center;
}
.load{
	font-size: 16px;
	font-family: 'Arial', sans-serif;
	color: @preload;
	position: absolute;
	top: 75%;
	left: 50%;
	transform: translateX(-50%);
	font-weight: 500;
}
.privacy-policy{
	a{
		color: @link;
	}
}
.item-sidebar--mob{
	.sm-block({
		display: none;
	});
}
.content-prod{
	.sm-block({
		display: none;
	});
}
.holder{
	width: 100%;
}